*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  height: 100%;
  font-size: 16px;
  font-family: "GolosText", sans-serif;
  font-weight: 400;
  color: #626262;
  overflow-x: hidden;
}

ul, ol {
  list-style-type: none;
  padding: 0;
}

a {
  display: block;
  font-family: inherit;
  text-decoration: none;
  color: inherit;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

a:active {
  outline: none;
}

a:focus {
  outline: none;
}

input {
  background: transparent;
  border: none;
  outline: none;
  transition: all .3s ease-in-out;
}

input:hover, input:active, input:focus {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
  outline: none;
}

button:active {
  outline: none;
}

button:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

select {
  outline: none;
}

select:focus {
  outline: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
  border: none;
  cursor: pointer;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 0;
  -webkit-text-fill-color: #CCCCCC;
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 174, 255, 0.04) 50%, rgba(255, 255, 255, 0) 51%, rgba(0, 174, 255, 0.03) 100%);
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex-shrink: 0;
}

.section {
  padding: 50px 0;
}

@media (max-width: 1024px) {
  .section {
    padding: 30px 0;
  }
}

.section--gray {
  background-color: #f5f5f5;
}

.section__title {
  margin-bottom: 37px;
  font-size: 30px;
  font-weight: 400;
}

.page {
  background: #626262;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-title {
  text-align: center;
  font-size: 38px;
  color: #FFFFFF;
}

@media (max-width: 768px) {
  .page-title {
    font-size: 26px;
  }
}

.logo {
  min-height: 40px;
  min-width: 178px;
}

.description {
  font-size: 17px;
}

.user {
  display: flex;
  margin-left: 50px;
}

@media (max-width: 1024px) {
  .user {
    margin-left: 0;
  }
}

@media (max-width: 1024px) {
  .header__btn:first-child {
    margin-left: 0;
  }
}

.subtitle {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 32px;
}

.subtitle--white {
  color: #FFFFFF;
}

.main {
  margin-top: 135px;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 129px;
  height: 44px;
  background-color: transparent;
  border: 1px solid #626262;
  border-radius: 3px;
  text-align: center;
  font-size: 14px;
}

.btn--orange {
  background-color: #ff9033;
  border: 1px solid #ff9033;
  color: #FFFFFF;
}

.btn--user {
  position: relative;
}

.btn--user:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  width: 18px;
  height: 21px;
  background: url("../img/user.png") no-repeat;
}

.btn--white {
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
}

.header-wrapp {
  position: fixed;
  min-height: 133px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  background: #FFFFFF;
  padding: 0 15px;
  margin-bottom: 0;
}

.header__box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px 0;
}

.header__btn {
  margin-left: 15px;
}

.header__lang {
  position: relative;
  display: flex;
  justify-content: flex-end;
  padding: 15px 0;
  border-bottom: 1px solid #e1e1e1;
}

@media (max-width: 1024px) {
  .header__lang {
    padding-right: 70px;
  }
}

.header__lang.open {
  position: static;
}

.header__links {
  display: flex;
}

.header__link {
  position: relative;
  padding: 0 5px;
  font-size: 18px;
  text-transform: uppercase;
}

.header__link--active {
  font-weight: bold;
}

.header__link--active:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 1px;
  height: 100%;
  background: #ff9033;
}

.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  display: flex;
  align-items: center;
}

@media (max-width: 1024px) {
  .nav {
    position: fixed;
    top: 0;
    right: -4000px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: #FFFFFF;
    width: 400px;
    height: 100vh;
    z-index: 3;
  }
  .nav.nav--show {
    right: 0;
  }
}

@media (max-width: 768px) {
  .nav {
    width: 100%;
  }
}

.nav-list {
  display: flex;
}

@media (max-width: 1024px) {
  .nav-list {
    flex-wrap: wrap;
  }
}

.nav-list__item {
  margin-left: 30px;
}

@media (max-width: 1024px) {
  .nav-list__item {
    width: 100%;
    margin-left: 0;
    margin-bottom: 20px;
  }
}

.nav-list__link {
  color: #626262;
  font-size: 18px;
  letter-spacing: 0.45px;
}

.burger {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  top: 20px;
  right: 0;
  background: transparent;
  width: 60px;
  cursor: pointer;
  z-index: 4;
  display: none;
}

@media (max-width: 1024px) {
  .burger {
    display: flex;
  }
}

.burger span {
  display: block;
  width: 60%;
  height: 1px;
  margin-bottom: 6px;
  background: #000000;
  transition: all .3s ease;
}

.burger span:first-child {
  width: 70%;
}

.burger span:last-child {
  width: 45%;
}

.burger:hover span {
  width: 65%;
  background: #ff9033;
}

.burger.open span {
  width: 70%;
  background: #ff9033;
}

.burger.open span:hover {
  background: #ff9033;
}

.burger.open span:last-child {
  display: none;
}

.burger.open span:first-child {
  transform: rotate(45deg);
  margin-bottom: 0;
}

.burger.open span:nth-child(2n) {
  transform: rotate(-45deg);
}

.banner {
  position: relative;
  padding: 50px 0;
  background: url("../img/austin.jpg") no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.banner-content {
  max-width: 537px;
}

@media (max-width: 1024px) {
  .banner-content {
    max-width: 100%;
  }
}

.banner-content__title {
  font-size: 18px;
  color: #ff9033;
  text-transform: uppercase;
  margin-bottom: 27px;
}

.banner-content__desc {
  font-size: 34px;
  font-weight: bold;
  text-transform: uppercase;
  color: #626262;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  .banner-content__desc {
    font-size: 28px;
  }
}

@media (max-width: 575px) {
  .banner-content__desc {
    font-size: 22px;
  }
}

.banner-content__text {
  color: #626262;
  margin-bottom: 40px;
}

.products__item {
  position: relative;
  z-index: 2;
  min-height: 441px;
  padding: 35px;
  margin-bottom: 30px;
  background-color: #ffffff;
  border-radius: 3px;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

@media (max-width: 768px) {
  .products__item {
    padding: 15px;
  }
}

.products__item:hover {
  box-shadow: 0 30px 64px rgba(0, 0, 0, 0.05);
}

.products__count {
  color: #e1e1e1;
  font-size: 74px;
  font-weight: bold;
  margin-bottom: 25px;
}

.products__title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 8px;
}

.products__subtitle {
  font-size: 13px;
  color: #ff9033;
  font-weight: bold;
  margin-bottom: 33px;
}

.products__desc {
  font-size: 16px;
  color: #576477;
  margin-bottom: 50px;
}

.products__item--bg::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 268px;
  background: url("../img/products-bg.png") no-repeat;
  z-index: -1;
}

.promo {
  align-items: center;
}

.promo__title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 24px;
